header {
    height: 100vh;
    padding-top: 22rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* SOCIALS */
.header__socials{
    display: flex;
    padding-top: 2rem;
    align-items: center;
    gap: 0.8rem;
    font-size: 1.5em;
    justify-content: center;
}
.header__socials::before{
    content: '';
    width: 7.5rem;
    height: 1px;
    background: var(--color-primary);
}

.header__socials::after{
    content: '';
    width: 7.5rem;
    height: 1px;
    background: var(--color-primary);
}

/* ME */

.me {
    width: 24rem;
    height: 30rem;
    position: relative;
    align-items: center;
    margin-top: -22rem;
    overflow: hidden;
}

/* Scroll down*/

.scroll__down {
    position: absolute;
    right: 0;
    bottom: 3rem;
    transform: rotate(90deg);
    font-weight: 500;
    font-size: 1.2rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width:1024px) {
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width:600px) {
    header {
        height: 100vh;
    }
    
    .header__socials, .scroll__down {
        display: none;
    }
}
