.contact__name{
    color: var(--color-primary);
    padding-bottom: 2rem;
}

.container.contact__container {
    width: 50%;
    text-align: center;
    display: block;
    gap: 12%;
}

form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}